import React from "react";
import { Link } from "react-router-dom";
import codepen from "../images/codepen.png";
import github from "../images/github.png";
import linkedin from "../images/linkedin.png";



const Footer = () =>(
  <React.Fragment>
    <footer className="navstyle">
      <div className="goback left" id="goback">
      <Link to="/">
        <i className="back-arrow"></i>
      </Link>
    </div>
    <div className="right">
          <a href="https://codepen.io/Pudel/" target="blank">
          <img src={codepen} alt="codepen"/>
          </a>
          <a href="https://www.linkedin.com/in/katrin-salac" target="blank">
             <img src={linkedin} alt="linkedin"/>
          </a>
          <a href="https://github.com/pudelgulasch" target="blank">
             <img src={github} alt="github"/>
          </a>
      </div>
      </footer>
    </React.Fragment>
    );

export default Footer;
